<template>
    <div class="coursesCategory">
        <div class="coursesCategoryDiv">
            <div class="coursesCategoryDiretion coursesSelection">
                <div class="coursesCategoryTitle"> 方向：</div>
                <div class="coursesSelectionCard">
                    <a class="selectionButtonA"
                       href="javascript:;"
                       v-for="title in coursesCategory"
                       :key="title['name']"
                       :class="currentCategory==title['name'] ? 'selected' : ''"
                       @click="changeCurrentCategory(title['name'])"
                    >
                        {{ title['name'] }}
                    </a>
                </div>
            </div>
            <div class="coursesCategoryTag coursesSelection">
                <div class="coursesCategoryTitle"> 标签： </div>
                <div class="coursesSelectionCard">
                    <div
                        v-for="tags in coursesCategory"
                        :key="tags['name']"
                    >
                        <a class="selectionButtonA"
                           href="javascript:;"
                           :class="currentTag=='全部' ? 'selected' : ''"
                           v-show="tags['name'].indexOf(currentCategory) != -1"
                           @click="changeCurrentTag('全部')"
                        >
                           全部
                        </a>
                        <a class="selectionButtonA"
                           href="javascript:;"
                           v-for="tagInformation in tags['tags']"
                           :key="tagInformation"
                           :class="currentTag==tagInformation ? 'selected' : ''"
                           v-show="tags['name'].indexOf(currentCategory) != -1"
                           @click="changeCurrentTag(tagInformation)"
                        >
                           {{ tagInformation }}
                        </a>
                    </div>
                </div>
            </div>
            <div class="coursesOrderDiv">
                <div class="leftOrderButtonDiv">
                    <div class="baseOrderDiv orderDiv">
                        <a class="orderButton"
                           href="javascript:;"
                           v-for="name in ['综合', '最新', '最热']"
                           :key="name"
                           :class="currentBaseOrder==name ? 'orderButtonSelected' : ''"
                           @click="changeCurrentBaseOrder(name)"
                        >
                           {{ name }}
                        </a>
                    </div>
                </div>
                <div class="rightOrderButtonDiv">
                    <div class="typeOrderDiv">
                        <a class="coursesSelectionSubSelectionA">
                            {{ currentTypeOrder }}
                        </a>
                        <ul id="subTypeOrderUl">
                            <li class="subTypeOrderLi" v-for="type in ['全部', '免费', '会员', '私有']">
                                <a href="javascript:;" class="subTypeOrderA"
                                @click="changeCurrentTypeOrder(type)"
                                >
                                    {{ type }}
                                </a>
                            </li>
                        </ul>
                    </div>
                    <a href="javascript:;" id="addToPersonCourses" class="addToPersonCoursesButton">
                        加入私有课
                    </a>
                </div>
            </div>
        </div>
        <div class="coursesDiv">
            <courseCard
                class="coursesSelectionCourseCard"
                v-for="(courseInfo, index) in coursesContent['results']"
                :key="index"
                :data="courseInfo"
            >

            </courseCard>
        </div>
        <tabPage :totalPage="totalPage" @changePage="changePage"></tabPage>
    </div>
</template>

<script type="text/javascript">
import coursesApi from '@/api/courses.js'
import courseCard from '@/components/common/courseCard.vue'
import tabPage from '@/components/common/tabPage.vue'


export default {
    components: {
        courseCard,
        tabPage
    },
    data() {
        return {
            coursesCategory: [],
            coursesContent: [],
            currentCategory: '全部',
            currentTag: '全部',
            currentBaseOrder: '综合',
            currentTypeOrder: '全部',
            baseOrderDict: {
                '综合': 'default',
                '最新': 'latest',
                '最热': 'hotest'
            },
            typeOrderDict: {
                '全部': 'all',
                '免费': 'free',
                '会员': 'member',
                '私有': 'privates'
            },
            totalPage: 1,
            currentPage: 1
        }
    },
    methods: {
        routerTo() {
            this.$router.push({
                name: 'courses',
                query: {
                    ...this.$route.query,
                    category: this.currentCategory,
                    tag: this.currentTag,
                    sort: this.baseOrderDict[this.currentBaseOrder],
                    fee: this.typeOrderDict[this.currentTypeOrder],
                    page: this.currentPage
                }
            })
        },
        changeCurrentCategory(name) {
            this.currentCategory = name
            this.currentTag = '全部'
            this.routerTo()
        },
        changeCurrentTag(name) {
            this.currentTag = name
            this.routerTo()
        },
        changeCurrentBaseOrder(name) {
            this.currentBaseOrder = name
            this.routerTo()
        },
        changeCurrentTypeOrder(name) {
            this.currentTypeOrder = name
            this.routerTo()
        },
        changePage(page) {
            this.currentPage = page
            this.routerTo()
        }
    },
    watch: {
        $route: async function () {
            let res = await coursesApi.getCoursesContent(this.$route.query)
            this.coursesContent = res.data
        }
    },
    async created() {

        this.currentCategory = this.$route.query.category ? this.$route.query.category : '全部'
        this.currentTag = this.$route.query.tag ? this.$route.query.tag : '全部'

        let res = await coursesApi.getCoursesCategories()
        this.coursesCategory = res.data

        let resTwo = await coursesApi.getCoursesContent(this.$route.query)
        this.coursesContent = resTwo.data
        this.totalPage = Math.ceil(resTwo.data.count / resTwo.data.page_size)
    }
}

</script>
<style scoped>
.coursesCategory {
    width: 1170px;
    padding: 0 5px;
    margin-left: auto;
    margin-right: auto;
}

/* 课程分类区域 */
.coursesCategoryDiv {
    width: 100%;
    background: #fff;
    padding: 20px;
    border: 1px solid #eee;
    margin-bottom: 10px;
}

.coursesCategoryTitle {
    font-size: 16px;
    color: #666;
    width: 62px;
    min-width: 62px;
}

.coursesSelection {
    display: flex;
    margin-bottom: 10px;
    /*flex-wrap: wrap;*/
}

.coursesCategoryTag {
    border-bottom: 2px solid #eee;

}

.coursesSelectionCard {
    display: flex;
    flex-wrap: wrap;
}

/* 分为左右两部分 */
.coursesOrderDiv {
    display: flex;
    justify-content: space-between;
    padding: 10px 0 0;
}

.leftOrderButtonDiv {
    display: flex;
    align-items: center;
}

.rightOrderButtonDiv {
    display: flex;
    /*flex-basis: baseline;*/
    align-items: baseline;
}

/* 排序按钮区域 */
.orderDiv {
    display: flex;
}

.baseOrderDiv {
    /*display: flex;*/
    margin-right: 50px;
}

.orderButton {
    margin-left: -1px;
}

/* type order 的二级子菜单及其父组件 */
.typeOrderDiv {
    position: relative;
    padding-right: 20px;
    padding-bottom: 10px;

}

.typeOrderDiv:hover #subTypeOrderUl{
    display: block;

}

.subTypeOrderLi {
    width: 100%;
    font-size: 14px;
    text-align: center;
    margin-bottom: 5px;
}

.subTypeOrderA, .coursesSelectionSubSelectionA {
    display: block;
    color: #3a3a3a;
    padding: 6px 0 0;
    font-size: 14px;
    cursor: pointer;
}

.coursesSelectionSubSelectionA:after {
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: .225rem;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    position: absolute;
    top: 50%;
    transform: translatey(-50%);
    right: 9px;
}

.subTypeOrderA:hover, .coursesSelectionSubSelectionA:hover{
    color: #08bf91;
}

#subTypeOrderUl {
    display: none;
    position: absolute;
    min-width: 105px;
    background: #fff;
    padding: 0;
    margin: 2px 0;
    box-shadow: 0 15px 16px 0 rgba(0,0,0,.18);
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    padding-bottom: 5px;
    right: -20px;
}

/* 加入私有课按钮 */
.addToPersonCoursesButton {
    display: block;
    font-size: 14px;
    color: #08bf91;
    background: #fff;
    padding: 3px 20px;
    border: 1px solid #08bf91;
    border-radius: 36px;
    box-shadow: none;
}

/* 课程 card 区域的样式 */

.coursesDiv {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
}

.coursesSelectionCourseCard {
    width: 25%;
    margin: 0 0 20px;
    padding: 0 15px;
}

/* 换页区域 */

.tabPageDiv {
    margin: 0 auto;
}

/* buttons */
.selectionButtonA {
    display: inline-block;
    margin: 0 8px 10px 0;
    padding: 4px 10px;
    font-size: 15px;
    color: #666;
    border-radius: 15px;
    cursor: pointer;
    transition: color 1s ease;
    transition: background .5s ease;
}

.selectionButtonA:hover {
    color: #fff;
    background: #08bf91;
}

.selected {
    color: #fff;
    background: #08bf91;
}

.orderButton {
    display: block;
    color: #999;
    border: 1px solid #c9c9c9;
    padding: 3px 5px;
    font-size: 14px;
}

.orderButton:hover, .orderButtonSelected {
    border: 1px solid #08bf91;
    color: #39cca7;
}
</style>