                    <img :src="data.picture_url" class="courseImgImg">
                </div>
                <div class="courseBody">
                    <div class="courseDescriptionTitleDiv">
                        {{ data.name }}
                    </div>
                    <div class="courseDescriptionDiv">
                        {{ data.description }}
                    </div>
                    <div class="courseInformationDiv">
                        <div>
                            <i class="fa fa-users"></i>
                            <span class="membersP">{{ data.students_count }}</span>
                        </div>
                        <p class="courseTypeP"
                        :class="courseTypeClass[data.fee_type]"
                        >{{ courseTypeName[data.fee_type] }}</p>
                    </div>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script type="text/javascript">
export default {
    data () {
        return {
            courseTypeClass: {
                'free': 'freeCourseP',
                'member': 'vipCourseP',
                'bootcamp': 'trainingCourseP',
                '': ''
            },
            courseTypeName: {
                'free': '免费',
                'member': '会员',
                'bootcamp': '训练营',
                '': ''
            }
        }
    },
    props: {
        data: {
            type: Object,
            require: true
        }
    }
}
</script>
<style type="text/css" scoped>
.courseCardDiv {
    min-height: 220px;
    box-sizing: border-box;
}

.courseDiv {
    width: 100%;
}

.courseImgDiv {
    width: 100%;
    height: 150px;
}

.courseImgImg {
    width: 100%;
}

.courseImgA {
    display: block;
    width: 100%;
    height: 100%;
    box-shadow: 0 1px 2px 0 #ddd;
}

.courseImgA:hover {
    box-shadow: 0 1px 30px 0 #d2d2d2;
}

.courseBody {
    position: relative;
    background: #fff;
    height: 100px;
    bottom: 0;
    width: 100%;
    transition: bottom .3s;
}

.courseImgA:hover .courseBody {
    bottom: 54.5px;
}

.courseImgA:hover .courseDescriptionDiv {
    height: 54.5px;
}

.courseDescriptionTitleDiv {
