}

.courseDescriptionDiv {
    height: 0;
    bottom: 0;
    padding: 0 10px;
    margin-top: 10px;
    font-size: 13px;
    color: #666;
    position: relative;
    overflow: hidden;
    transition: height .3s;
}

.courseInformationDiv {
    height: 43px;
    color: #9b9da2;
    width: 100%;
    margin-top: 10px;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.courseTypeP {
    min-height: 23px;
    color: #fff;
    padding: 3px 10px;
    border-radius: 100px;
}

.freeCourseP {
    background: #59cf4a;
}

.trainingCourseP {
    background: #f66e6e;
}

.vipCourseP {
    background: #ffae0f;
}
</style>